import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    height: 0,
    width: 0,
    scrollY: 0,
    is_movil: false,
  },
  mutations: {
  },
  actions: {},
  getters: {
    get_height(state) {
      return state.get_height;
    },
    get_width(state) {
      return state.width;
    },
    get_scrollY(state) {
      return state.scrollY;
    },
    get_isMovil(state) {
      return state.is_movil;
    },
  },
});
