import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Homepage",
    component: () => import("../views/Homepage.vue"),
  },
  {
    path: "/*",
    name: "Forbidden",
    component: () => import("../views/Forbidden.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: "/",
  routes,
});

export default router;
