import Vue from 'vue'
import Vuetify from 'vuetify/lib'

Vue.use(Vuetify)

const opts = {
    theme: {
        dark: false,
        default: 'light',
        disable: false,
        options: {
          cspNonce: undefined,
          customProperties: undefined,
          minifyTheme: undefined,
          themeCache: undefined,
        },
        themes: {
          light: {
            primary_app:'#A40018',
            secondary_app: '#231F20',
            accent_app: '#212429',
          },
        },
      },
}

export default new Vuetify(opts)